import {
	CardActions,
	Typography,
	CardHeader,
	Card,
	Divider,
	CardContent,
	Button,
	TextField,
	Grid,
	LinearProgress,
	FormControlLabel,
	Switch,
	Slide,
} from "@material-ui/core";
import { useState, useEffect } from "react";
import { useSnackbar } from "notistack";
import Hint from "./Hint.js";
import utils from "../utils";
import { useHistory } from "react-router-dom";

const Question = (props) => {
	let title, description, id, puzzle;
	if (props.question) {
		title = props.question.title;
		description = props.question.description;
		id = props.question._id;
		puzzle = props.question.puzzle;
	}

	const [hint, handleHintChange] = useState(false);
	const [hintKey, setHintKey] = useState(false);
	const [hintData, setHintData] = useState(null);
	const { enqueueSnackbar } = useSnackbar();
	const history = useHistory();

	useEffect(() => {
		handleHintChange(false);
		setHintKey(false);
		setHintData(null);
	}, [props.question]);

	const generateKey = () => {
		utils
			.setKey()
			.then((res) => {
				setHintKey(res.key);
				enqueueSnackbar(
					"Congrats!! you got your hint key, Click on get hint to get your hint.",
					{
						anchorOrigin: {
							vertical: "top",
							horizontal: "center",
						},
						variant: "success",
						TransitionComponent: Slide,
					}
				);
			})
			.catch((err) => {
				const error = err.response;
				if (error.status === 403) {
					utils.logOut();
					history.push("/end");
				} else {
					enqueueSnackbar("Something went wrong !!", {
						anchorOrigin: {
							vertical: "top",
							horizontal: "center",
						},
						variant: "error",
						TransitionComponent: Slide,
					});
				}
			});
	};

	const getHint = () => {
		utils
			.getHint(hintKey)
			.then((res) => {
				setHintData(res.hint);
				setHintKey(false);
			})
			.catch((err) => {
				const error = err.response;
				if (error.status === 403) {
					utils.logOut();
					history.push("/end");
				} else {
					enqueueSnackbar("Something went wrong !!", {
						anchorOrigin: {
							vertical: "top",
							horizontal: "center",
						},
						variant: "error",
						TransitionComponent: Slide,
					});
				}
			});
	};

	return (
		<Card>
			<CardContent>
				{props.question ? (
					<>
						<CardHeader
							title={
								<Typography variant="h5" component="p">
									{title}
								</Typography>
							}
						/>
						<Divider variant="middle" />
						<br />
						{hint ? (
							<>
								<Typography variant="body1" display="block" align="center">
									Solve puzzle get secret key to your hint.
								</Typography>
								<Typography
									align="center"
									variant="body1"
									display="block"
									gutterBottom
									paragraph
								>
									Going back to Question will reset the puzzle!!
								</Typography>

								<Hint
									hintData={hintData}
									hintKey={hintKey}
									setHintKey={generateKey}
									getHint={getHint}
									puzzle={puzzle}
									id={id}
								/>
							</>
						) : (
							<Typography variant="subtitle1" component="p">
								{description}
							</Typography>
						)}

						<br />
						<Divider variant="middle" />

						<CardActions>
							<Grid
								container
								spacing={3}
								justify="flex-start"
								alignItems="flex-start"
							>
								<Grid item xs={4}>
									<form
										noValidate
										autoComplete="off"
										onSubmit={(e) => {
											e.preventDefault();
											let ans = e.target[0].value.toLowerCase();
											props.checkAnswer(id, ans);
										}}
									>
										<TextField
											variant="outlined"
											margin="normal"
											required
											fullWidth
											id="ans"
											label="Answer"
											name="ans"
											autoFocus
										/>
										<Button
											required
											type="submit"
											variant="contained"
											style={{
												backgroundColor: "#9fe6a0",
											}}
											size="medium"
										>
											Submit
										</Button>
									</form>
								</Grid>
								<Grid item xs={4}>
									<FormControlLabel
										control={
											<Switch
												checked={hint}
												onChange={() => handleHintChange(!hint)}
												name="hint"
												color="primary"
											/>
										}
										label="Hint"
										labelPlacement="top"
									/>
								</Grid>
							</Grid>
						</CardActions>
					</>
				) : (
					<LinearProgress />
				)}
			</CardContent>
		</Card>
	);
};

export default Question;
