import { Grid, Typography, Card, CircularProgress } from "@material-ui/core";
import { useState, useEffect, useContext } from "react";
import utils from "../utils";
import SessionContext from "../SessionContext";

const Timer = (props) => {
	const { score } = props;

	const [time, setTime] = useState(null);
	const [over, setOver] = useState(false);
	const { setSessionData } = useContext(SessionContext);

	if (!time) {
		(async () => {
			try {
				const temp = await utils.getTime();
				const initTime = temp.data.time;
				setTime(initTime);
			} catch (e) {
				utils.logOut();
				setSessionData(null);
			}
		})();
	}

	const tick = () => {
		if (over) return;
		if (!time) setOver(true);
		else {
			setTime(time - 1000);
		}
	};

	useEffect(() => {
		const timer = setInterval(tick, 1000);
		return () => clearInterval(timer);
	});

	const timeObj = getTimeObject(time);
	return (
		<Grid
			container
			direction="row"
			justify="flex-start"
			alignItems="center"
			spacing={1}
		>
			<Grid item xs={5}>
				<Card style={{ padding: "5%" }}>
					<Typography variant="subtitle1">Time</Typography>

					<Typography variant="h4">
						{time ? (
							`${timeObj.h}:${timeObj.m}:${timeObj.s}`
						) : (
							<CircularProgress />
						)}
					</Typography>
				</Card>
			</Grid>
			<Grid item xs={4}>
				<Card style={{ padding: "5%" }}>
					<Typography variant="subtitle1">Score</Typography>
					<Typography variant="h4">{score}</Typography>
				</Card>
			</Grid>
		</Grid>
	);
};

function getTimeObject(time) {
	if (!time) return;
	//Convert millseconds to hours, minutes, seconds
	let s = Math.floor(time / 1000) % 60;
	let m = Math.floor((time / (1000 * 60)) % 60);
	let h = Math.floor((time / (1000 * 60 * 60)) % 24);

	h = h < 10 ? `0${h}` : h;
	m = m < 10 ? `0${m}` : m;
	s = s < 10 ? `0${s}` : s;

	return {
		h,
		m,
		s,
	};
}

export default Timer;
