import axios from "axios";
import { v4 as uuidv4 } from "uuid";

const utils = {
	saveSession: (session, user) => {
		localStorage.setItem("session", JSON.stringify({ ...session, ...user }));
	},

	getSessionData: () => JSON.parse(localStorage.getItem("session")),
	logIn: (data) => {
		return axios.post("/users/login", data).then((res) => res.data);
	},
	getTime: async () => {
		const res = axios.get("/api/time");
		return await res;
	},
	saveSessionScore: (score) => {
		const prvsSession = JSON.parse(localStorage.getItem("session"));
		const newSession = { ...prvsSession, score };
		localStorage.setItem("session", JSON.stringify(newSession));
	},
	saveQuestionState: (nextId) => {
		const oldSession = JSON.parse(localStorage.getItem("session"));
		oldSession.currentQuestion = nextId;
		localStorage.setItem("session", JSON.stringify(oldSession));
	},

	logOut: () => {
		localStorage.removeItem("session");
		axios.get("/users/logout");
	},

	register: (data) => {
		return axios.post("/users/register", data).then((res) => res.data);
	},

	getList: () => {
		return axios.get("/api/list").then((res) => res.data);
	},

	getQuestion: (nextId, prvsId, ans) => {
		return axios
			.post("/api/question", { nextId, prvsId, ans })
			.then((res) => res.data);
	},
	setKey: () => {
		const key = uuidv4();
		return axios.post("/api/setKey", { key }).then((res) => res.data);
	},
	getHint: (key) => axios.post("/api/getHint", { key }).then((res) => res.data),

	getPuzzle: (id) =>
		axios.post("/api/getPuzzle", { id }).then((res) => res.data),
	checkPuzzleAnswer: (ans, id) =>
		axios.post("/api/checkPuzzle", { id, ans }).then((res) => res.data),
};

export default utils;
