import { useState } from "react";
import { Button, Grid, Typography, Divider } from "@material-ui/core";
import Sudoku from "react-sudoku-component";
import Puzzle from "../Components/Puzzle";

const Hint = (props) => {
	console.log(props);
	return (
		<>
			<Grid
				container
				direction="row"
				justify="center"
				alignItems="center"
				spacing={2}
			>
				{!props.hintData && (
					<Grid item xs={12}>
						{props.puzzle ? (
							<Puzzle
								id={props.id}
								puzzle={props.puzzle}
								onSolved={props.setHintKey}
							/>
						) : (
							<Sudoku
								defaultDifficulty="easy"
								disableTimer
								disableDifficultyButtons
								disableNumberButtons
								// disableHelpButtons
								onSolved={props.setHintKey}
							/>
						)}

						<br />
						<Divider variant="middle" />
					</Grid>
				)}

				{props.hintData ? (
					<Grid item xs={12}>
						<Typography variant="h6" align="center" gutterBottom>
							Hint
						</Typography>
						<Typography variant="body1" align="center" gutterBottom>
							{props.hintData}
						</Typography>
					</Grid>
				) : (
					<Grid item xs={12}>
						<Typography variant="h6" align="center" gutterBottom>
							Secret Key
						</Typography>
						<Typography variant="body1" align="center" gutterBottom>
							{props.hintKey ? props.hintKey : "Solve puzzle to get your key!"}
						</Typography>
					</Grid>
				)}

				<Grid item>
					<Button
						disabled={!props.hintKey || props.hintData}
						onClick={props.getHint}
						type="submit"
						variant="contained"
						size="medium"
					>
						Get Hint
					</Button>
				</Grid>
			</Grid>
		</>
	);
};

export default Hint;
